// Nav Bar Styles
$background: #284e77;
$font-color: #ff7f78;
$hover-color: #ddefd7;

// @import "../../MediaQueries.scss";

.navbar {
  top: 0;
  z-index: 2;
  position: fixed;
  overflow: hidden;
  height: 70px;
  width: 100vw;
  // background-color: $background;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.navbar > ul {
  display: flex;
  flex-direction: row;
  list-style-type: none;
  font-family: "Oswald", sans-serif;
  font-weight: 800;
  text-transform: uppercase;
  font-size: 1.45em;
}

.navbar > ul > li {
  margin-right: 40px;
  margin-top: 20px;
}

.navbar > ul > li > a {
  text-decoration: none;
  color: $font-color;
}

.navbar > ul > li > a:hover {
  color: $hover-color;
}

.navbar > ul > li > a:active {
  color: $hover-color;
}

.navbar > ul > li > a:visited {
  font-weight: 500;
}

